<script setup>
import { ref, computed, onMounted, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import AppInput from "@/components/utility/text-input/index.vue";
import AppSelect from "@/components/utility/select/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppToggle from "@/components/utility/toggle/index.vue";
import RichTextEditor from "@/components/utility/rich-text/index.vue";
import Multiselect from "@/components/utility/multiselect-roles-and-users/index.vue";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { useAppStore } from "@/store/app/app.js";
import { notify } from "@/assets/js/functions/notify.js";

const route = useRoute();
const router = useRouter();
const appStore = useAppStore();
const isEdit = computed(() => !!route.params.noticeid);
const loading = ref(false);
const saving = ref(false);
const notice = ref({
  title: "",
  content: "",
  meta: {
    notice_type: "info",
    roles: [],
    dismissible: true,
    active: true,
  },
});

const noticeTypes = [
  { value: "info", label: __("Info", "uixpress") },
  { value: "success", label: __("Success", "uixpress") },
  { value: "warning", label: __("Warning", "uixpress") },
  { value: "error", label: __("Error", "uixpress") },
];

const seenUsers = ref([]);

/**
 * Fetch user info for all users in seen_by
 */
const fetchSeenUsers = async (userIds) => {
  if (!userIds || !userIds.length) {
    seenUsers.value = [];
    return;
  }
  // Use the include param to fetch all users in one request
  const args = {
    endpoint: `wp/v2/users`,
    params: { include: userIds.join(",") }
  };
  const res = await lmnFetch(args);
  if (res && Array.isArray(res.data)) {
    seenUsers.value = res.data.map(user => ({
      id: user.id,
      name: user.name || user.username || user.slug,
      email: user.email,
      avatar: user.avatar_urls?.['24'] || user.avatar_urls?.['48'] || user.avatar_urls?.['96'] || '',
    }));
  } else {
    seenUsers.value = [];
  }
};

const fetchNotice = async () => {
  if (!isEdit.value) return;
  loading.value = true;
  appStore.updateState("loading", true);
  const args = { endpoint: `wp/v2/uixpress-notices/${route.params.noticeid}` };
  const data = await lmnFetch(args);
  if (data?.data) {
    notice.value = {
      title: data.data.title?.rendered || "",
      content: data.data.content?.rendered || "",
      meta: {
        notice_type: data.data.meta?.notice_type || "info",
        roles: data.data.meta?.roles || [],
        dismissible: data.data.meta?.dismissible !== false,
        active: data.data.status == 'draft' ? false : true,
      },
    };
  }

  fetchSeenUsers(data.data.meta?.seen_by || []);

  loading.value = false;
  appStore.updateState("loading", false);
};

const saveNotice = async () => {
  saving.value = true;
  appStore.updateState("loading", true);
  const args = {
    endpoint: isEdit.value ? `wp/v2/uixpress-notices/${route.params.noticeid}` : "wp/v2/uixpress-notices",
    type: isEdit.value ? "POST" : "POST",
    data: {
      title: notice.value.title,
      content: notice.value.content,
      status: notice.value.meta.active ? 'publish' : 'draft',
      meta: {
        notice_type: notice.value.meta.notice_type,
        roles: notice.value.meta.roles,
        dismissible: notice.value.meta.dismissible,
      },
    },
  };

  const data = await lmnFetch(args);

  saving.value = false;
  appStore.updateState("loading", false);

  if (!data) {
    return;
  }


  notify({ type: "success", title: __("Notice updated", "uixpress") });
};

const goBack = () => {
  router.push({ name: "admin-notices-table" });
};

onMounted(fetchNotice);
</script>

<template>
  <div class=" mx-auto bg-white dark:bg-zinc-900 rounded-lg p-8">
    <div class="mb-6 flex flex-row items-center justify-between">
      <h2 class="text-xl font-semibold">
        {{ __("Edit Notice", "uixpress") }}
      </h2>

      <div class=" flex flex-row items-center gap-3">

        <AppButton type="default" @click="goBack">{{ __("Back", "uixpress") }}</AppButton>

        <AppButton type="primary" :loading="saving" @click="saveNotice">
          {{ __("Update Notice", "uixpress") }}
        </AppButton>
      </div>
    </div>


    <div class="grid grid-cols-3 gap-12">
      <!-- edit column-->
      <div class="col-span-2">
        <div class="mb-4">
          <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Title", "uixpress") }}</label>
          <AppInput v-model="notice.title" type="text" :placeholder="__('Notice title', 'uixpress')" />
        </div>
        <div class="mb-4">
          <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Content", "uixpress") }}</label>
          <RichTextEditor v-model="notice.content" :placeholder="__('Notice content', 'uixpress')" />
        </div>
        <div class="mb-4">
          <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Type", "uixpress") }}</label>
          <AppSelect v-model="notice.meta.notice_type" :options="noticeTypes" />
        </div>
        <div class="mb-4">
          <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Roles", "uixpress") }}</label>
          <Multiselect v-model="notice.meta.roles" class="max-w-[300px]" />
        </div>
        <div class="mb-4 flex flex-row gap-6">
          <div>
            <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Dismissible", "uixpress")
              }}</label>
            <AppToggle v-model="notice.meta.dismissible" />
          </div>
          <div>
            <label class="block text-zinc-700 dark:text-zinc-200 font-medium mb-1">{{ __("Active", "uixpress")
              }}</label>
            <AppToggle v-model="notice.meta.active" />
          </div>
        </div>
        <div class="mt-8 flex flex-row gap-4">
          <AppButton type="primary" :loading="saving" @click="saveNotice">
            {{ __("Update Notice", "uixpress") }}
          </AppButton>
          <AppButton type="default" @click="goBack">{{ __("Cancel", "uixpress") }}</AppButton>
        </div>
      </div>

      <!-- preview column-->
      <div>
        <div class="font-semibold mb-2">{{ __('Dismissed by', 'uixpress') }} </div>
        <div v-if="seenUsers.length === 0" class="text-zinc-400 text-sm">
          {{ __('No users have dismissed this notice yet.', 'uixpress') }}
        </div>
        <div v-else class="flex flex-col gap-2">
          <div v-for="user in seenUsers" :key="user.id" class="flex flex-row items-center gap-2">
            <img :src="user.avatar" class="w-7 h-7 rounded-full border border-zinc-200 dark:border-zinc-700"
              :alt="user.name" />
            <span class="text-zinc-700 dark:text-zinc-200 text-sm">{{ user.name }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>